  items: any[] = []; // Array to store
  itemsForm: FormGroup;
  itemErrors: { [key: string]: string }[] = [];

  handleFileInput(files: FileList) {
    const file = files.item(0);

    if (file) {
      const fileReader = new FileReader();
      fileReader.onload = (e: any) => {
        const arrayBuffer = e.target.result;
        const workbook = XLSX.read(arrayBuffer, { type: "array" });
        const worksheet = workbook.Sheets[workbook.SheetNames[0]];
        const jsonData = XLSX.utils.sheet_to_json(worksheet, { raw: true });

        this.items = jsonData;

        // this.items = [
        //   {
        //     itemName: "Sample Item 1",
        //     itemQuantity: 5,
        //     itemDescription: "Sample description for Item 1",
        //     unitOfMeasure: "pcs",
        //     needByDate: "2023-06-23",
        //   },
        //  
        // ];

        console.log(" this.items :", this.items);

        // Initialize the form
        this.itemsForm = this.fb.group({
          items: this.fb.array([]), // Array of items
        });

        // Call the function to validate the items
        this.validateItems();
      };

      fileReader.readAsArrayBuffer(file);
    }
  }

  validateItems() {
    this.itemErrors = []; // Clear previous errors
    const items = this.items;

    const itemsFormArray = this.itemsForm.get("items") as FormArray; // Create a separate FormArray variable

    for (const item of items) {
      const formGroup = this.fb.group({
        itemName: [{ value: item.itemName }, [Validators.required]],
        itemQuantity: [{ value: item.itemQuantity }, [Validators.required]],
        itemDescription: [
          { value: item.itemDescription },
          [Validators.required],
        ],
        unitOfMeasure: [{ value: item.unitOfMeasure }, [Validators.required]],
        needByDate: [{ value: item.needByDate }, [Validators.required]],
      });

      itemsFormArray.push(formGroup); // Use push method on the FormArray variable
      this.collectErrors(formGroup);

      if (formGroup.valid) {
        // Rest of the code remains the same
        this.bgtForm.patchValue({
          itemName: item.itemName,
          itemQuantity: item.itemQuantity,
          itemDescription: item.itemDescription,
          unitOfMeasure: item.unitOfMeasure,
          needByDate: item.needByDate,
        });

        // Rest of the code remains the same

        this.dataSource.data.push(this.bgtForm.value);
        this.dataSource.data = this.dataSource.data.slice();
        this.bgtForm.reset();
      }
    }
  }

  collectErrors(control: AbstractControl, path: string = "") {
    if (control instanceof FormGroup) {
      Object.keys(control.controls).forEach((key) => {
        const subControl = control.get(key);
        const subPath = this.getErrorPath(path, key);
        this.collectErrors(subControl, subPath);
      });
    } else {
      const errors = control.errors;
      if (errors) {
        const itemError = {
          field: path,
          message: this.getErrorMessage(errors),
        };
        this.itemErrors.push(itemError);
      }
    }
  }

  getErrorPath(path: string, key: string): string {
    return path ? `${path}.${key}` : key;
  }

  getErrorMessage(errors: any): string {
    if (errors.required) {
      return "This field is required.";
    } else if (errors.pattern) {
      return "Invalid value.";
    }
    return "";
  }

