  // ****************************************************************************************************************************************

  catalogueArray: [] = [];

  selectedItem: any[] = [];
  catalogueLookup() {
    if (this.mngForm.value.requisitionCategory === "Stock") {
      const dialogConfig = new MatDialogConfig();
      dialogConfig.disableClose = false;
      dialogConfig.disableClose = true;
      dialogConfig.width = "880px";
      dialogConfig.data = {
        action: "single item",
        selected: this.selectedItem,
      };

      const dialogRef = this.dialog.open(
        CatalogueLookupComponent,
        dialogConfig
      );

      dialogRef.afterClosed().subscribe((result) => {
        console.log("Result:", result);
        if (result && result.data && result.data.length > 0) {
          const firstItem = result.data[0];
          console.log("Name:", firstItem);

          this.bgtForm.patchValue({
            itemCode: firstItem.itemCode,
            itemName: firstItem.name,
            unitOfMeasure: firstItem.unit,
            itemQuantity: firstItem.quantity,
            itemDescription: firstItem.description,

            // expenseCode: firstItem.expenseCode
          });
        }
      });
    } else if (this.mngForm.value.requisitionCategory === "FixedAsset") {
      const dialogConfig = new MatDialogConfig();
      dialogConfig.disableClose = false;
      dialogConfig.disableClose = true;
      dialogConfig.width = "880px";
      dialogConfig.data = {
        action: "single item",
        selected: this.selectedItem,
      };

      const dialogRef = this.dialog.open(
        AssetCatalogLookupComponent,
        dialogConfig
      );

      dialogRef.afterClosed().subscribe((result) => {
        console.log("Result:", result);
        if (result && result.data && result.data.length > 0) {
          const firstItem = result.data[0];
          console.log("Name:", firstItem);

          this.bgtForm.patchValue({
            itemCode: firstItem.itemCode,
            itemName: firstItem.name,
            unitOfMeasure: firstItem.unit,
            itemQuantity: firstItem.quantity,
            itemDescription: firstItem.description,

            // expenseCode: firstItem.expenseCode
          });
        }
      });
    } else {
      this.snackbar.showNotification(
        "snackbar-danger",
        "Catalogue can only be used for Stock and Fixed Asset Items!"
      );
    }
  }

  //********************************************************************************************************************************** */
  posting: boolean = false;
  onSubmit() {
    console.log("this.mngForm.value: ", this.mngForm.value);
    this.posting = true;
    if (
      this.mngForm.value.needItems &&
      this.mngForm.value.needItems.length > 0
    ) {
      if (this.pageFunction == "Add") {
        this.needService
          .addNeeds(this.mngForm.value)
          .pipe(takeUntil(this.destroy$))
          .subscribe({
            next: (res) => {
              console.log("res: ", res);

              if (res.statusCode == 200) {
                this.snackbar.showNotification(
                  "snackbar-success",
                  "Need added successfully"
                );
                this.router.navigate(["/erp-procurement/needs/all-needs"]);
              } else {
                this.snackbar.showNotification("snackbar-danger", res.message);
              }
            },
            error: (err) => {
              this.snackbar.showNotification("snackbar-danger", err.message);
            },
            complete: () => {
              this.posting = false;
            },
          }),
          Subscription;
      } else if (this.pageFunction == "Update") {
        this.needService
          .updateNeeds(this.mngForm.value)
          .pipe(takeUntil(this.destroy$))
          .subscribe({
            next: (res) => {
              if (res.statusCode == 200) {
                this.snackbar.showNotification(
                  "snackbar-success",
                  "Need updated successfully"
                );
                this.router.navigate(["/erp-procurement/needs/all-needs"]);
              } else {
                this.snackbar.showNotification("snackbar-danger", res.message);
              }
            },
            error: (err) => {
              this.snackbar.showNotification("snackbar-danger", err.message);
            },
            complete: () => {
              this.posting = false;
            },
          }),
          Subscription;
      }
    } else {
      this.snackbar.showNotification(
        "snackbar-danger",
        "Requisition Items cannot be null!!"
      );
    }
  }

  cancel() {
    this.router.navigate(["/erp-procurement/needs/all-needs"]);
  }

  // **********************************************************************************************
  // onValidate(){

  //   // "HOD Need Validation",
  //   // "Finance Need Validation",
  //   // "Procurement Need Validation"

  // }

  hasAccess: boolean;

  onValidate(userType) {
    let privilege = "";

    switch (userType) {
      case "HOD":
        privilege = "HOD Need Validation";
        break;

      case "PROCUREMENT":
        privilege = "Procurement Need Validation";
        break;
      default:
        return;
    }

    this.hasAccess = this.accessControlService.hasPrivilege(
      "ProcurementModule",
      [privilege]
    );

    if (this.hasAccess) {
      const dialogConfig = new MatDialogConfig();
      dialogConfig.disableClose = false;
      dialogConfig.autoFocus = true;
      dialogConfig.width = "650px";
      dialogConfig.data = {
        data: this.formData,
        userType: userType,
      };

      const dialogRef = this.dialog.open(ValidateNeedComponent, dialogConfig);

      dialogRef.afterClosed().subscribe((result) => {
        this.router.navigate(["/erp-procurement/needs/all-needs"]);
      });
    }
  }

