import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { environment } from "src/environments/environment.prod";



@Injectable({
  providedIn: "root",
})
export class BudgetsService {



  constructor(private http: HttpClient) { }

  getBudgets(): Observable<any> {
    const BudgetManagementUrl = `${environment.baseUrlFinance}/api/v1/budgets/lines/all`;
    return this.http.get<any[]>(BudgetManagementUrl);
  }
  getBudgetsByHodAndFinanceStatus(params): Observable<any> {
    const BudgetManagementUrl = `${environment.baseUrlFinance}/api/v1/budgets/lines/fetchbyhodandfinancestatus`;
    return this.http.get<any[]>(BudgetManagementUrl, { params });
  }
  getBudgetsByStatus(params): Observable<any> {
    const BudgetManagementUrl = `${environment.baseUrlFinance}/api/v1/budgets/lines/fetchbyStatus`;
    return this.http.get<any[]>(BudgetManagementUrl, { params });
  }
  getBudgetsByCostCenterCodeAndStatus(params): Observable<any> {
    const BudgetManagementUrl = `${environment.baseUrlFinance}/api/v1/budgets/lines/fetchbycostcentercodeandstatus`;
    return this.http.get<any[]>(BudgetManagementUrl, { params });
  }

  getBudgetsByYear(params): Observable<any> {
    const BudgetManagementUrl = `${environment.baseUrlFinance}/api/v1/budgets/lines/all-by-year`;
    return this.http.get<any[]>(BudgetManagementUrl, { params });
  }
  getBudgetsById(params: any): Observable<any> {
    return this.http.get<any>(
      `${environment.baseUrlFinance}/api/v1/budgets/lines/find/by/id`, {params}
    );
  }

  getBudget(): Observable<any> {
    const BudgetManagementUrl = `${environment.baseUrlFinance}/api/v1/budgets/lines/all`;
    return this.http.get<any[]>(BudgetManagementUrl);
  }
  addBudget(data: any): Observable<any> {
    const BudgetManagementUrl = `${environment.baseUrlFinance}/api/v1/budgets/lines/create`;
    return this.http.post<any>(BudgetManagementUrl, data);
  }
  extractBulkBudgets(params: any, data: any): Observable<any> {
    const BudgetManagementUrl = `${environment.baseUrlFinance}/api/v1/budgets/lines/excel`;
    return this.http.post<any>(BudgetManagementUrl, data,{params});
  }

  addBudgets(data: any): Observable<any> {
    const BudgetManagementUrl = `${environment.baseUrlFinance}/api/v1/budgets/lines/create/bulk`;
    return this.http.post<any>(BudgetManagementUrl, data);
  }

  updateBudgets(BudgetDetails: any): Observable<any> {
    const updateBudgetUrl = `${environment.baseUrlFinance}/api/v1/budgets/expenses/update-expense`;
    return this.http.put<any>(updateBudgetUrl, BudgetDetails);
  }

  updateBudgetExps(BudgetDetails: any): Observable<any> {
    const updateBudgetUrl = `${environment.baseUrlFinance}/api/v1/budgets/lines/update-expense`;
    return this.http.put<any>(updateBudgetUrl, BudgetDetails);
  }

  deleteBudgetTemporarily(params: any): Observable<any> {
    const BudgetManagementUrl = `${environment.baseUrlFinance}/api/v1/budgets/lines/delete`;
    return this.http.delete<any>(BudgetManagementUrl, {params});
  }

  validateBudgets(params: any, dataToValidate: any): Observable<any> {
    const imprestUrl = `${environment.baseUrlFinance}/api/v1/budgets/lines/update/state`;
    return this.http.put<any>(imprestUrl, dataToValidate, {params});
  }

  


  budgetCheck(data: any): Observable<any> {
    const BudgetManagementUrl = `${environment.baseUrlFinance}/api/v1/budgets/lines/check`;
    return this.http.post<any>(BudgetManagementUrl, data);
  }


 
  uploadExcelPath(excelFilePath: string) {
    return this.http.post<any>('http://localhost:9191/generateBudget', { excelFilePath });
  }


  generateBudget(file: File): Observable<any> {
    const formData: FormData = new FormData();
    formData.append('file', file, file.name);
    return this.http.post(`http://localhost:9191/generateBudget`, formData);
  }
}
