import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AllNeedsComponent } from './pages/all-needs/all-needs.component';
import { ManageNeedComponent } from './pages/manage-need/manage-need.component';
import { RoutePrivilegeGuard } from '../../data/services/_AccessControlAuthGuard.service';

const routes: Routes = [
  {
    path: "all-needs",
    component: AllNeedsComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {  clientName: 'ProcurementModule', requiredPrivilege:["All Needs"] },
  },
  {
    path: "manage-need",
    component: ManageNeedComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {  clientName: 'ProcurementModule', requiredPrivilege:["Manage Needs"] },
  },
 

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class NeedsRoutingModule { }
