import { DatePipe } from '@angular/common';
import { HttpParams } from '@angular/common/http';
import { Component, Inject, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBarHorizontalPosition, MatSnackBarVerticalPosition } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { Subject, takeUntil } from 'rxjs';
import { TokenCookieService } from 'src/app/core/service/token-storage-cookies.service';
import { SnackbarService } from 'src/app/shared/services/snackbar.service';
import { ManageNeedComponent } from '../../pages/manage-need/manage-need.component';
import { NeedService } from 'src/app/erp-procurement/data/services/need.service';


@Component({
  selector: 'app-validate-need',
  templateUrl: './validate-need.component.html',
  styleUrls: ['./validate-need.component.sass']
})
export class ValidateNeedComponent implements OnInit {

  horizontalPosition: MatSnackBarHorizontalPosition = "end";
  verticalPosition: MatSnackBarVerticalPosition = "top";



  Data: any;

  status!: string;

  resData: any;

  statusForm: FormGroup;

  rejected: boolean = false;
  approved: boolean = false;
  currentUser: any;
  postedBy: any;
  canVerify: boolean = false;

  validationIsLoading: boolean = false;
  hideValidation = false;

    destroy$: Subject<boolean> = new Subject<boolean>();

  downloadLoading: boolean = false;

  constructor(
    public dialogRef: MatDialogRef<ManageNeedComponent>,
    @Inject(MAT_DIALOG_DATA) public data,
    private fb: FormBuilder,
    private snackbar: SnackbarService,
    private tokenCookieService: TokenCookieService,
    private needService: NeedService,
    private router: Router,
    private datepipe: DatePipe,
  ) {
    this.Data = this.data.data;
  }
  ngOnInit(): void {
    this.currentUser = this.tokenCookieService.getUser().username;


    if (this.data.action === 'deletedOrRejected') {
      this.hideValidation = true;
    }



    this.postedBy = this.Data.postedBy;

    this.canVerify = true;

    // if (this.postedBy === this.currentUser) {

    //   this.snackbar.showNotification("snackbar-danger", "You cannot verify this transaction!");

    //   this.canVerify = false;

    // } else {
    //   this.canVerify = true;
    // }

    console.log("this.billDet: ", this.data);

    console.log("Data: ", this.Data);

    this.statusForm = this.createStatusForm();
  }
  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }


  createStatusForm(): FormGroup {
    return this.fb.group({
      reason: ["", [Validators.required]],
    });
  }

  reject() {
    this.rejected = true;
    this.approved = false;

    this.status = "REJECTED";
    if (!this.statusForm.value == null) {
      this.changeStatus();
    }
  }
  return() {
    this.rejected = true;
    this.approved = false;

    this.status = "RETURNED";
    if (!this.statusForm.value == null) {
      this.changeStatus();
    }

  }
  approve() {
    this.approved = true;
    this.rejected = false;

    this.status = "APPROVED";
    this.statusForm.patchValue({
      reason: "NA"
    })

    this.changeStatus();
  }



  changeStatus() {
    this.validationIsLoading = true;
  
    let todayDate = this.datepipe.transform(
      new Date(),
      "yyyy-MM-ddTHH:mm:ss"
    );
  

    const params = new HttpParams()

      .set("id", this.Data.id)
      .set("userType", this.data.userType)
      .set("action", this.status)
      .set("reason", this.statusForm.value.reason)

    console.log("Form = ", params);
  
    this.needService
      .validateNeed(params)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          if (res.statusCode == 200) {
            this.snackbar.showNotification(
              "snackbar-success",
              "Need validated successfully"
            );
            
          }
          else {
            this.snackbar.showNotification("snackbar-danger", res.message);
          }
        },
        error: (err) => {
          console.log("err = ", err);
          this.snackbar.showNotification(
            "snackbar-danger",
            "Server Error: " + err.message
          );
        },
        complete: () => {
          this.dialogRef.close();
          this.router.navigate(["/erp-procurement/needs/all-needs"]);
        }
      });
  }

  

}
