import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Observable, map } from "rxjs";
import { environment } from "src/environments/environment.prod";



@Injectable({
  providedIn: "root",
})
export class NeedService {

  constructor(private http: HttpClient) { }

  getNeeds(): Observable<any> {
    const NeedssUrl = `${environment.baseUrlProcurement}/api/v1/requisition/need/all`;
    return this.http.get<any[]>(NeedssUrl);
  }

  getNeedsByStatus(params: any): Observable<any> {
    const NeedssUrl = `${environment.baseUrlProcurement}/api/v1/requisition/need/all/by/hod_status/procurement_status`;
    return this.http.get<any[]>(NeedssUrl, {
      params: params,
    });
  }

  getNeedsById(params: any): Observable<any> {
    const NeedssUrl = `${environment.baseUrlProcurement}/api/v1/requisition/need/find/by/id`;
    return this.http.get<any>(NeedssUrl, { params: params, });
  }

  addNeeds(data: any): Observable<any> {
    const NeedssUrl = `${environment.baseUrlProcurement}/api/v1/requisition/need/add`;
    return this.http.post<any>(NeedssUrl, data);
  }

  updateNeeds(NeedsDetails: any): Observable<any> {
    const updateBillUrl = `${environment.baseUrlProcurement}/api/v1/requisition/need/update`;
    return this.http.put<any>(updateBillUrl, NeedsDetails)
  }

  deleteNeedsTemporarily(params: any): Observable<any> {
    const NeedssUrl = `${environment.baseUrlProcurement}/api/v1/requisition/need/delete`;
    return this.http.delete<any>(NeedssUrl, { params });
  }



  validateNeed(params: any): Observable<any> {
    const API_URL = `${environment.baseUrlProcurement}/api/v1/requisition/need/update/state`;
    return this.http.put(
      API_URL,
      {},
      {
        params: params,
      }
    );
  }


  // ********************************************************************************************************************

  donwloadRequsitionReport(params: any): Observable<any> {
    let headers = new HttpHeaders();
    headers.append("Accept", "application/pdf");
    let requestOptions: any = {
      params: params,
      headers: headers,
      responseType: "blob",
      withCredentials: false,
    };
    let API_URL = `${environment.baseUrlProcurement}/api/v1/report/download/need_requisition`;
    return this.http.get(API_URL, requestOptions).pipe(
      map((response: BlobPart) => {
        console.log("Hey this is download Requsitition Report", response);
        return {
          filename: "Report",
          data: new Blob([response], { type: "application/pdf" }),
        };
      })
    );
  }

}
