import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { environment } from "src/environments/environment.prod";

@Injectable({
  providedIn: "root",
})
export class GlAccountsService {
  constructor(private http: HttpClient) {}

  getGLAccounts(): Observable<any> {
    const GLAccountManagementUrl = `${environment.baseUrlFinance}/api/v1/gls/all`;
    return this.http.get<any[]>(GLAccountManagementUrl);
  }
  getGLAccountsByStatus(params): Observable<any> {
    const GLAccountManagementUrl = `${environment.baseUrlFinance}/api/v1/gls/fetchbyStatus`;
    return this.http.get<any[]>(GLAccountManagementUrl, { params });
  }


  getGLAccountsByFilterParameters(params): Observable<any> {
    const GLAccountManagementUrl = `${environment.baseUrlFinance}/api/v1/gls/findByFilter`;
    return this.http.get<any[]>(GLAccountManagementUrl, { params });
  }

  getGLAccountsById(params: any): Observable<any> {
    return this.http.get<any>(
      `${environment.baseUrlFinance}/api/v1/gls/find/by/id`, {params}
    );
  }

  getGLAccountByGlName(params: any): Observable<any> {
    return this.http.get<any>(
      `${environment.baseUrlFinance}/api/v1/gls/find/by/acdescription`, {params}
    );
  }

  getGLAccountByExpAndCostCenter(params: any): Observable<any> {
    return this.http.get<any>(
      `${environment.baseUrlFinance}/api/v1/expenses/find/by/costcenter/and/expensecode`, {params}
    );
  }

  addGLAccount(data: any): Observable<any> {
    const GLAccountManagementUrl = `${environment.baseUrlFinance}/api/v1/gls/create`;
    return this.http.post<any>(GLAccountManagementUrl, data);
  }

  addGLAccounts(data: any): Observable<any> {
    const GLAccountManagementUrl = `${environment.baseUrlFinance}/api/v1/gls/create/mock/bulk`;
    return this.http.post<any>(GLAccountManagementUrl, data);
  }
  addNewGLAccounts(data: any): Observable<any> {
    const GLAccountManagementUrl = `${environment.baseUrlFinance}/api/v1/gls/map-budget`;
    return this.http.post<any>(GLAccountManagementUrl, data);
  }

  updateGLAccounts(GLAccountDetails: any): Observable<any> {
    const updateGLAccountUrl = `${environment.baseUrlFinance}/api/v1/gls/update`;
    return this.http.put<any>(updateGLAccountUrl, GLAccountDetails);
  }

  deleteGLAccountTemporarily(params: any): Observable<any> {
    const GLAccountManagementUrl = `${environment.baseUrlFinance}/api/v1/gls/delete`;
    return this.http.delete<any>(GLAccountManagementUrl, {params});
  }

  validateAccounts(dataToValidate: any): Observable<any> {
    const imprestUrl = `${environment.baseUrlFinance}/api/v1/gls/update/state`;
    return this.http.put<any>(imprestUrl, dataToValidate);
  }

  // ********************************************************************************************************************

  initiateCoreSynchronization(): Observable<any> {
    const GLAccountManagementUrl = `${environment.baseUrlFinance}/api/v1/gls/balances/synchronize`;
    return this.http.post<any>(GLAccountManagementUrl, {});
  }
}
