  addNeed() {
    this.router.navigate(["/erp-procurement/needs/manage-need"]);
  }

  editNeed(data: any) {
    const additionalData = data;
    //const serializedData = JSON.stringify(additionalData);

    let route = "/erp-procurement/needs/manage-need";
    this.router.navigate([route], {
      queryParams: { id: data.id, action: "Update" },
    });
  }
  viewNeed(data: any) {
    const additionalData = data;
    //const serializedData = JSON.stringify(additionalData);
    let route = "/erp-procurement/needs/manage-need";
    this.router.navigate([route], {
      queryParams: { id: data.id, action: "View" },
    });
  }
  validateNeed(data: any) {
    const additionalData = data;
    // const serializedData = JSON.stringify(additionalData);
    let route = "/erp-procurement/needs/manage-need";
    this.router.navigate([route], {
      queryParams: { id: data.id, action: "Validate" },
    });
  }

  downloadReqReport(row: any) {
    this.downloadLoading = true;

    const params = new HttpParams().set("needCode", row.needCode);

    this.needService
      .donwloadRequsitionReport(params)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          console.log("res :", res);
          let url = window.URL.createObjectURL(res.data);

          // if you want to open PDF in new tab
          window.open(url);

          let a = document.createElement("a");
          document.body.appendChild(a);
          a.setAttribute("style", "display: none");
          a.setAttribute("target", "blank");
          a.href = url;
          // a.download = ".pdf";
          a.download = `RequsitionReport_${row.needCode}.pdf`;
          a.click();
          window.URL.revokeObjectURL(url);
          a.remove();

          this.downloadLoading = false;

          this.snackbar.showNotification(
            "snackbar-success",
            "Report generated successfully"
          );
          // if (res.statusCode === 200) {
          // } else {
          //   this.snackbar.showNotification("snackbar-danger", res.message);
          // }
        },
        error: (err) => {
          this.snackbar.showNotification(
            "snackbar-danger",
            "Error doanloading PO!"
          );
          this.downloadLoading = false;
        },
        complete: () => {
          this.downloadLoading = false;
        },
      }),
      Subscription;
  }

  deleteNeed(id: number) {
    Swal.fire({
      title: "Are you sure?",
      text: "Do you really want to delete this Need?",
      icon: "question",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, Delete Need!",
    }).then((result) => {
      if (result.isConfirmed) {
        const params = new HttpParams()
          .set("id", id)
          .set("username", this.currentUser);
        this.needService
          .deleteNeedsTemporarily(params)
          .pipe(takeUntil(this.destroy$))
          .subscribe({
            next: (res) => {
              this.snackbar.showNotification(
                "snackbar-success",
                "Need Deleted Successfully"
              );
            },
            error: (err) => {
              this.snackbar.showNotification("snackbar-danger", err.message);
            },
            complete: () => {
              this.getData(this.currentPage, this.pageSize);
            },
          }),
          Subscription;
      }
    });
  }

